body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: sans-serif;
}

.wrap {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  position: relative;
}

h1.absolute-label {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  font-size: 74px;
  width: 60px; 
  overflow: hidden;
  text-align: center;
  animation:h1sized 1 2.5s;
	animation-fill-mode: forwards;
  text-transform: uppercase;
  transition: ease 0.5s;
  color: red;
  mix-blend-mode: multiply;
  font-family: "Righteous", cursive;
  transform: translate(-50%, -100%);
}
@keyframes h1sized{
	0%{
		width: 60px;
	}
	50%{
		width: 60px;
	}
	 
	100%{
		width: 344px;
	}
}
.controls{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  transition: linear .4s;
  justify-content: center;
  overflow: hidden;
  flex-direction: column;
   
}
.controls.stacked{
  width: 0;
  transition: linear .4s;

}
.controls-btns {
   min-width: 50%;
   display: flex;
   align-items: center;
   justify-content: space-between;
   margin: 5px 0;
   font-size: 15px;
}
.controls-btns button {
  border:none;
  background: transparent;
  font-size: 15px;
}
.controls-btns button:hover{
  cursor: pointer;
}
h1.unfold {
  display: none;
  font-family: "Righteous", cursive;
}

.left-side {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  transition: linear 0.4s;
  opacity: 0.9;
}
.left-wrap {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 25px 50px;
}
.left-wrap_columns {
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: space-around;
}
.column {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 1.4;
}
.column b {
  text-transform: capitalize;
  margin: 5px 0;
}
.hide {
  transition: linear 0.4s;
  width: 0 !important;
  opacity: 0;
}

.right-side {
  width: 50%;
  overflow-y: scroll;
  height: 100%;
  transition: linear 0.4s;
  display: flex;
  flex-direction: column;
}

section {
  background: #ccc;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
}

.hidden-section {
  background: #000;
  width: 50%;
  transition: linear 0.4s;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.fixed {
  position: absolute;
  height: 100vh;
  width: 50%;
  z-index: 555;
}

section#one {
  background: linear-gradient(#1e130c, #9a8478);
}
section#two {
  background: linear-gradient(#9a8478, #948e99);
}
section#three {
  background: linear-gradient(#948e99, #2e1437);
}
section#four {
  background: linear-gradient(#2e1437, #360033);
}
section#five {
  background: linear-gradient(#360033, #0b8793);
}
section#six {
  background: linear-gradient(#0b8793, #40777c);
}
section#seven {
  background: linear-gradient(#40777c, #476962);
}
section.locked {
  background: linear-gradient(#476962, #948e99);
}

.action-img {
  min-width: 50%;
  transition: linear 0.3s;
  max-height: 60%;
}

.action-img:hover {
  cursor: pointer;
  transition: linear 0.3s;
  box-shadow: 0 0 3px white;
}

.hide-label {
  opacity: 0;
  transition: linear 0.4s;
}

.scroll-hidden {
  overflow: hidden;
}

.left-side h2 {
  width: 80%;
  height: 56px;
  transition: linear 0.3s;
  overflow: hidden;
}

.text-hidden {
  width: 0;
  height: 56px;
  transition: linear 0.3s;
  overflow: hidden;
}

@media (max-width: 878px) {
  body {
    overflow: unset;
  }
  .wrap {
    flex-direction: column;
    min-height: unset;
    height: max-content;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  h1.absolute-label {
    display: none;
  }
  h1.unfold {
    display: inline;
  }
  .left-wrap {
    justify-content: space-around;
    padding: 5px 15px 35px;
    overflow: hidden;
  }
  .left-wrap h2 {
    text-align: center;
  }
  .left-side {
    width: 100%;
    height: 100vh;
    /* here  */
  }
  .right-side {
    width: 100%;
    overflow-y: unset;
    height: auto;
  }
  section {
    height: 100vh;
    margin-bottom: -1px;
  }
  .hide.hidden-section {
    width: 100%;
    height: 0;
    transition: linear 0.3s;
    position: fixed;
    background: #000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
    overflow: hidden;
  }
  .hidden-section {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
    overflow: hidden;
  }
  .fixed {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
  }
  .locked {
    display: none;
  }
}
